angular.module('mapp')
	.controller('homeCtrl',homeCtrl);
homeCtrl.$injector = ['$scope'];
function homeCtrl($scope){
	let myswiper = new Swiper('.swiper-container');

	$scope.fn = function(e)
	{
		var lis = e.target.parentNode.children;
		myswiper.slideTo(2);
		for(let j = 0;j<lis.length;j++){
			lis[j].className = '';
			lis[j].onclick = function(){
				//myswiper.slideTo(j);
			}
		}

	}
	
}


